import { useState } from "react";
import { useDispatch, useSelector } from 'react-redux';
import { loginThunk, selectAuth } from '../store/authSlice';

const API_BASE = process.env.REACT_APP_API_URL?.replace(/\/$/, "") || "";

export default function Login({ onLoggedIn, onForgot }) {
  const [form, setForm] = useState({ email: "", password: "" });
  const dispatch = useDispatch();
  const { status } = useSelector(selectAuth);
  const loading = status === 'loading';
  const [blockInfo, setBlockInfo] = useState(null);

  const change = (e) => setForm({ ...form, [e.target.name]: e.target.value });

  const submit = async (e) => {
    e.preventDefault();
    setBlockInfo(null);
    const action = await dispatch(loginThunk({ email: form.email, password: form.password }));
    if (loginThunk.fulfilled.match(action)) {
      onLoggedIn?.(action.payload.user);
    } else if (action.payload?.status === 429 && action.payload?.data?.blocked) {
      setBlockInfo({
        message: action.payload.data.message,
        remainingMinutes: action.payload.data.remainingMinutes,
        adminEmail: action.payload.data.adminContactEmail,
      });
    } else {
      alert(action.payload?.message || 'ng nhp tht bi');
    }
  };

  return (
    <div>
      {blockInfo && (
        <div style={{
          background: '#fff3cd',
          border: '2px solid #ffc107',
          borderRadius: '6px',
          padding: '16px',
          marginBottom: '20px',
        }}>
          <div style={{ 
            fontWeight: 'bold', 
            color: '#856404', 
            marginBottom: '12px',
            fontSize: '16px',
          }}>
             Ti khon b kha
          </div>
          <div style={{ color: '#856404', marginBottom: '12px' }}>
            {blockInfo.message}
          </div>
          <div style={{
            background: 'white',
            padding: '12px',
            borderRadius: '4px',
            marginTop: '12px',
          }}>
            <div style={{ 
              fontWeight: '600', 
              color: '#dc3545',
              marginBottom: '8px',
            }}>
               Lin h Admin  m kha:
            </div>
            <a 
              href={`mailto:${blockInfo.adminEmail}?subject=Yu cu m kha ti khon&body=Xin cho Admin,%0D%0A%0D%0ATi khon ca ti (${form.email})  b kha do ng nhp sai qu nhiu ln.%0D%0A%0D%0AVui lng h tr m kha ti khon.%0D%0A%0D%0AXin cm n!`}
              style={{
                color: '#007bff',
                textDecoration: 'none',
                fontWeight: '500',
                fontSize: '16px',
              }}
            >
              {blockInfo.adminEmail}
            </a>
          </div>
          <div style={{ 
            marginTop: '12px',
            fontSize: '14px',
            color: '#666',
          }}>
             Hoc i {blockInfo.remainingMinutes} pht  th li
          </div>
        </div>
      )}
      
      <form onSubmit={submit} className="form">
        <div className="field">
          <label className="label">Email</label>
          <input className="input" name="email" type="email" value={form.email} onChange={change} required />
        </div>
        <div className="field">
          <label className="label">Mt khu</label>
          <input className="input" name="password" type="password" value={form.password} onChange={change} required />
        </div>

        <div className="row">
          <button className="button" type="submit" disabled={loading}>{loading ? "ang ng nhp..." : "ng nhp"}</button>
        </div>
      </form>
    </div>
  );
}
