import React from "react";

export default function ProfileView({ user }) {
  if (!user) return null;
  const dobStr = user.dob ? new Date(user.dob).toLocaleDateString() : "-";
  return (
    <div className="card">
      <div className="card__header">Thng tin c nhn</div>
      <div className="card__body">
        {user.avatarUrl && (
          <div style={{ marginBottom: 12 }}>
            <img src={user.avatarUrl} alt="avatar" style={{ width: 96, height: 96, objectFit: 'cover', borderRadius: 12, border: '1px solid var(--border)' }} />
          </div>
        )}
        <div className="field"><span className="label">H tn</span><div>{user.name}</div></div>
        <div className="spacer" />
        <div className="field"><span className="label">Email</span><div>{user.email}</div></div>
        <div className="spacer" />
        <div className="field"><span className="label">Ngy sinh</span><div>{dobStr}</div></div>
        <div className="spacer" />
        {user.phone && (<><div className="field"><span className="label">S in thoi</span><div>{user.phone}</div></div><div className="spacer" /></>)}
        {user.address && (<><div className="field"><span className="label">a ch</span><div>{user.address}</div></div><div className="spacer" /></>)}
        {user.avatarUrl && (<><div className="field"><span className="label">nh</span><div><a href={user.avatarUrl} target="_blank" rel="noreferrer">M nh</a></div></div><div className="spacer" /></>)}
        
        <div className="field"><span className="label">Vai tr</span><div>{user.role}</div></div>
      </div>
    </div>
  );
}
