import { useEffect, useState } from 'react';
import api from '../lib/api';

export default function ResetPassword({ defaultEmail = '', onDone }) {
  const [form, setForm] = useState({ email: defaultEmail, otp: '', newPassword: '' });
  const [confirm, setConfirm] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [remaining, setRemaining] = useState(120); // seconds for OTP validity
  const [resending, setResending] = useState(false);

  const change = (e) => setForm({ ...form, [e.target.name]: e.target.value });

  useEffect(() => {
    setRemaining(120);
    const id = setInterval(() => {
      setRemaining((s) => (s > 0 ? s - 1 : 0));
    }, 1000);
    return () => clearInterval(id);
  }, []);

  const submit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage('');
    if (form.newPassword.length < 6) {
      setLoading(false);
      setMessage('Mt khu mi phi t 6 k t tr ln');
      return;
    }
    if (form.newPassword !== confirm) {
      setLoading(false);
      setMessage('Xc nhn mt khu khng khp');
      return;
    }
    try {
      await api.post('/auth/reset-password', form);
      setMessage('t li mt khu thnh cng.');
      onDone?.();
    } catch (err) {
      setMessage(err?.response?.data?.message || 't li tht bi');
    } finally {
      setLoading(false);
    }
  };

  const resend = async () => {
    if (!form.email) {
      setMessage('Vui lng nhp email  gi li OTP');
      return;
    }
    setResending(true);
    setMessage('');
    try {
      await api.post('/auth/forgot-password', { email: form.email });
      setRemaining(120);
      setMessage(' gi li OTP. OTP c hiu lc 2 pht.');
    } catch (err) {
      setMessage(err?.response?.data?.message || 'Gi li OTP tht bi');
    } finally {
      setResending(false);
    }
  };

  return (
    <form onSubmit={submit} className="form">
      <div className="field">
        <label className="label">Email</label>
        <input className="input" name="email" type="email" value={form.email} onChange={change} required />
      </div>
      <div className="field">
        <label className="label">OTP</label>
        <input className="input" name="otp" value={form.otp} onChange={change} placeholder="6 k t" inputMode="numeric" pattern="^\\d{6}$" title="Nhp 6 ch s" required />
      </div>
      <div className="field">
        <label className="label">Mt khu mi</label>
        <input className="input" name="newPassword" type="password" value={form.newPassword} onChange={change} required minLength={6} />
      </div>
      <div className="field">
        <label className="label">Nhp li mt khu mi</label>
        <input className="input" name="confirmPassword" type="password" value={confirm} onChange={(e) => setConfirm(e.target.value)} required minLength={6} />
        {form.newPassword && confirm && form.newPassword !== confirm && (
          <div className="empty" style={{ color: '#c62828', marginTop: 6 }}>Mt khu nhp li khng khp</div>
        )}
      </div>
      <div className="row">
        <button className="button" type="submit" disabled={loading}>{loading ? 'ang x l...' : 't li mt khu'}</button>
      </div>
      <div className="row" style={{ marginTop: 8, gap: 8, alignItems: 'center' }}>
        <span className="badge">OTP cn hiu lc: {Math.floor(remaining / 60)}:{String(remaining % 60).padStart(2,'0')}</span>
        <button type="button" className="button button--ghost" onClick={resend} disabled={resending}>
          {resending ? 'ang gi li...' : 'Gi li OTP'}
        </button>
      </div>
      {message && <div className="empty" style={{ marginTop: 8 }}>{message}</div>}
    </form>
  );
}
