import React, { useEffect, useState } from "react";
import api from "../lib/api";

export default function ResetPasswordByToken({ defaultToken = "", onDone }) {
  const [token, setToken] = useState(defaultToken);
  const [newPassword, setNewPassword] = useState("");
  const [confirm, setConfirm] = useState("");
  const [loading, setLoading] = useState(false);
  const [msg, setMsg] = useState("");
  const [done, setDone] = useState(false);

  useEffect(() => {
    if (!defaultToken) {
      const params = new URLSearchParams(window.location.search);
      const t = params.get('resetToken') || params.get('token') || '';
      if (t) setToken(t);
    }
  }, [defaultToken]);

  const submit = async (e) => {
    e.preventDefault();
    if (!token) { setMsg('Thiu token'); return; }
    if (!newPassword || newPassword.length < 6) { setMsg('Mt khu ti thiu 6 k t'); return; }
    if (newPassword !== confirm) { setMsg('Xc nhn mt khu khng khp'); return; }
    setLoading(true);
    setMsg("");
    try {
      const res = await api.post(`/auth/resetpassword/${encodeURIComponent(token)}`, { newPassword });
      setMsg(res?.data?.message || 't li mt khu thnh cng. Nhn OK  quay li trang ng nhp.');
      setDone(true);
    } catch (e) {
      setMsg(e?.response?.data?.message || 't li mt khu tht bi');
    } finally {
      setLoading(false);
    }
  };

  if (done) {
    return (
      <div className="form">
        <div className="empty" style={{ marginTop: 8 }}>{msg || 't li mt khu thnh cng. Nhn OK  quay li trang ng nhp.'}</div>
        <div className="row">
          <button className="button" type="button" onClick={() => onDone?.()}>OK</button>
        </div>
      </div>
    );
  }

  return (
    <form onSubmit={submit} className="form">
      {!defaultToken && (
        <div className="field">
          <label className="label">Token</label>
          <input className="input" value={token} onChange={(e) => setToken(e.target.value)} placeholder="Dn token t email vo y" />
        </div>
      )}
      <div className="field">
        <label className="label">Mt khu mi</label>
        <input className="input" type="password" value={newPassword} onChange={(e) => setNewPassword(e.target.value)} minLength={6} required />
      </div>
      <div className="field">
        <label className="label">Nhp li mt khu</label>
        <input className="input" type="password" value={confirm} onChange={(e) => setConfirm(e.target.value)} minLength={6} required />
        {newPassword && confirm && newPassword !== confirm && (
          <div className="empty" style={{ color: '#c62828', marginTop: 6 }}>Mt khu nhp li khng khp</div>
        )}
      </div>
      <div className="row">
        <button className="button" type="submit" disabled={loading}>{loading ? 'ang i...' : 'i mt khu'}</button>
      </div>
      {msg && <div className="empty" style={{ marginTop: 8 }}>{msg}</div>}
    </form>
  );
}
