import { useEffect, useRef, useState } from 'react';
import api from '../lib/api';

export default function ChangePasswordAdmin() {
  const [otpMsg, setOtpMsg] = useState('');
  const [pw, setPw] = useState({ otp: '', newPassword: '', confirm: '' });
  const [sending, setSending] = useState(false);
  const [submitting, setSubmitting] = useState(false);
  const [remaining, setRemaining] = useState(0);
  const timerRef = useRef(null);

  useEffect(() => () => { if (timerRef.current) clearInterval(timerRef.current); }, []);

  const sendOtp = async () => {
    setSending(true);
    setOtpMsg('');
    try {
      await api.post('/auth/me/forgot-password');
      setOtpMsg(' gi OTP n email ca bn (2 pht).');
      setRemaining(120);
      if (timerRef.current) clearInterval(timerRef.current);
      timerRef.current = setInterval(() => {
        setRemaining((s) => {
          if (s <= 1) { clearInterval(timerRef.current); timerRef.current = null; return 0; }
          return s - 1;
        });
      }, 1000);
    } catch (e) {
      setOtpMsg(e?.response?.data?.message || 'Khng gi c OTP');
    } finally {
      setSending(false);
    }
  };

  const submit = async (e) => {
    e.preventDefault();
    setSubmitting(true);
    setOtpMsg('');
    try {
      if (pw.newPassword.length < 6) throw new Error('Mt khu mi ti thiu 6 k t');
      if (pw.newPassword !== pw.confirm) throw new Error('Xc nhn mt khu khng khp');
      await api.post('/auth/me/reset-password', { otp: pw.otp, newPassword: pw.newPassword });
      setOtpMsg('i mt khu thnh cng');
      setPw({ otp: '', newPassword: '', confirm: '' });
    } catch (e) {
      setOtpMsg(e?.response?.data?.message || e.message || 'i mt khu tht bi');
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <>
      <div className="empty" style={{ marginBottom: 8, fontSize: 12, opacity: 0.8 }}>
        Qun tr  i mt khu
      </div>
      <div className="card">
      <div className="card__header">i mt khu (Admin)</div>
      <div className="card__body">
        <div className="row" style={{ gap: 8 }}>
          <button className="button" type="button" onClick={sendOtp} disabled={sending}>
            {sending ? 'ang gi OTP...' : 'Gi OTP n email ca ti'}
          </button>
          {remaining > 0 && (
            <span className="badge">OTP cn: {Math.floor(remaining/60)}:{String(remaining%60).padStart(2,'0')}</span>
          )}
        </div>
        <form className="form" onSubmit={submit} style={{ marginTop: 12 }}>
          <div className="field">
            <label className="label">OTP</label>
            <input className="input" value={pw.otp} onChange={(e) => setPw({ ...pw, otp: e.target.value })} placeholder="6 ch s" inputMode="numeric" pattern="^\\d{6}$" title="Nhp 6 ch s" required />
          </div>
          <div className="field">
            <label className="label">Mt khu mi</label>
            <input className="input" type="password" value={pw.newPassword} onChange={(e) => setPw({ ...pw, newPassword: e.target.value })} minLength={6} required />
          </div>
          <div className="field">
            <label className="label">Nhp li mt khu mi</label>
            <input className="input" type="password" value={pw.confirm} onChange={(e) => setPw({ ...pw, confirm: e.target.value })} minLength={6} required />
          </div>
          <div className="row">
            <button className="button" type="submit" disabled={submitting}>{submitting ? 'ang i...' : 'i mt khu'}</button>
          </div>
          {otpMsg && <div className="empty" style={{ marginTop: 8 }}>{otpMsg}</div>}
        </form>
      </div>
    </div>
    </>
  );
}
