import React, { useEffect, useRef, useState } from "react";
import api from "../lib/api";

export default function ProfileForm({ initialUser, onSaved, isAdminEditing, userId, focusChangePassword = false, allowSelfChangePassword = false }) {
  const [form, setForm] = useState({ name: "", dob: "", address: "", phone: "", avatarUrl: "", role: "" });
  const [saving, setSaving] = useState(false);
  const [showExtras, setShowExtras] = useState(false);
  const [showChangePwd, setShowChangePwd] = useState(allowSelfChangePassword && !!focusChangePassword);
  const [otpMsg, setOtpMsg] = useState("");
  const [devOtp, setDevOtp] = useState("");
  const [pw, setPw] = useState({ otp: "", newPassword: "", confirm: "" });
  const [cpLoading, setCpLoading] = useState(false);
  const [remaining, setRemaining] = useState(0);
  const [resending, setResending] = useState(false);
  const otpIntervalRef = useRef(null);
  const fileRef = useRef(null);
  const [uploading, setUploading] = useState(false);

  useEffect(() => {
    if (initialUser) {
      setForm({
        name: initialUser.name || "",
        dob: initialUser.dob ? new Date(initialUser.dob).toISOString().slice(0,10) : "",
        address: initialUser.address || "",
        phone: initialUser.phone || "",
        avatarUrl: initialUser.avatarUrl || "",
        role: initialUser.role || "user",
      });
    }
  }, [initialUser]);

  useEffect(() => {
    if (allowSelfChangePassword && focusChangePassword) setShowChangePwd(true);
  }, [allowSelfChangePassword, focusChangePassword]);

  useEffect(() => {
    return () => {
      if (otpIntervalRef.current) {
        clearInterval(otpIntervalRef.current);
        otpIntervalRef.current = null;
      }
    };
  }, []);

  const isModeratorSelf = !isAdminEditing && (initialUser?.role === 'moderator');
  const change = (e) => setForm({ ...form, [e.target.name]: e.target.value });

  const submit = async (e) => {
    e.preventDefault();
  if (isModeratorSelf) return; // read-only for moderator
  setSaving(true);
    try {
      const payload = { name: form.name };
      if (form.dob) payload.dob = form.dob;
      if (showExtras) {
        payload.address = form.address;
        payload.phone = form.phone;
        payload.avatarUrl = form.avatarUrl;
      }
      if (isAdminEditing && userId) {
        // Only admin can change role; include if present
        if (form.role) payload.role = form.role;
        // Admin updates another user
        await api.put(`/users/${userId}`, payload);
      } else {
        // Self update
        await api.put(`/auth/me`, payload);
      }
      onSaved?.();
    } catch (err) {
      alert(err?.response?.data?.message || "Cp nht tht bi");
    } finally {
      setSaving(false);
    }
  };

  const sendOtp = async () => {
    setResending(true);
    setOtpMsg("");
    setDevOtp("");
    try {
      const res = await api.post('/auth/me/forgot-password');
      setOtpMsg(' gi OTP n email ca bn (hiu lc 2 pht).');
      if (res?.data?.otp) setDevOtp(String(res.data.otp));
      setRemaining(120);
      if (otpIntervalRef.current) {
        clearInterval(otpIntervalRef.current);
      }
      otpIntervalRef.current = setInterval(() => {
        setRemaining((s) => {
          if (s <= 1) {
            if (otpIntervalRef.current) {
              clearInterval(otpIntervalRef.current);
              otpIntervalRef.current = null;
            }
            return 0;
          }
          return s - 1;
        });
      }, 1000);
    } catch (e) {
      setOtpMsg(e?.response?.data?.message || 'Khng gi c OTP');
    } finally {
      setResending(false);
    }
  };

  const changePassword = async (e) => {
    e.preventDefault();
    if (!pw.newPassword || pw.newPassword.length < 6) { setOtpMsg('Mt khu mi ti thiu 6 k t'); return; }
    if (pw.newPassword !== pw.confirm) { setOtpMsg('Xc nhn mt khu khng khp'); return; }
    setCpLoading(true);
    setOtpMsg("");
    try {
      await api.post('/auth/me/reset-password', { otp: pw.otp, newPassword: pw.newPassword });
      setOtpMsg('i mt khu thnh cng');
      setPw({ otp: "", newPassword: "", confirm: "" });
    } catch (e) {
      setOtpMsg(e?.response?.data?.message || 'i mt khu tht bi');
    } finally {
      setCpLoading(false);
    }
  };

  return (
    <form onSubmit={submit} className="form">
      <div className="field">
        <label className="label">H tn</label>
        <input className="input" name="name" value={form.name} onChange={change} required disabled={isModeratorSelf} />
      </div>
      {isAdminEditing && (
        <div className="field">
          <label className="label">Phn quyn</label>
          <select className="input" name="role" value={form.role || 'user'} onChange={change}>
            <option value="user">User</option>
            <option value="moderator">Moderator</option>
            <option value="admin">Admin</option>
          </select>
          <div className="row" style={{ marginTop: 6 }}>
            <span className={`chip ${form.role === 'admin' ? 'chip--danger' : form.role === 'moderator' ? 'chip--accent' : 'chip--muted'}`}>
              Quyn: {form.role || 'user'}
            </span>
          </div>
          <div className="empty" style={{ marginTop: 6, fontSize: 12, opacity: 0.8 }}>
            Ch Admin mi c quyn phn quyn. H thng ch cho php duy nht 1 ti khon Admin.
          </div>
        </div>
      )}
      <div className="field">
        <label className="label">Ngy sinh</label>
        <input className="input" name="dob" type="date" value={form.dob} onChange={change} required disabled={isModeratorSelf} />
      </div>
      {!isModeratorSelf && (
        <div className="row">
          <button className="button button--ghost" type="button" onClick={() => setShowExtras(s => !s)}>
            {showExtras ? 'n b sung' : 'B sung thng tin'}
          </button>
        </div>
      )}
      {showExtras && !isModeratorSelf && (
        <>
          <div className="field">
            <label className="label">nh c nhn</label>
            <div style={{ display: 'flex', alignItems: 'center', gap: 12, flexWrap: 'wrap' }}>
              <div style={{ width: 68, height: 68, borderRadius: 8, overflow: 'hidden', background: '#1b2230', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                {form.avatarUrl ? (
                  <img src={form.avatarUrl} alt="avatar" style={{ width: '100%', height: '100%', objectFit: 'cover' }} />
                ) : (
                  <span style={{ fontSize: 12, opacity: 0.7 }}>No image</span>
                )}
              </div>
              <div style={{ display: 'flex', gap: 8, alignItems: 'center', flexWrap: 'wrap' }}>
                <button type="button" className="button" onClick={() => fileRef.current?.click()} disabled={uploading}>
                  {uploading ? 'ang ti...' : ' Ti nh ln'}
                </button>
                <input ref={fileRef} type="file" accept="image/*" style={{ display: 'none' }} onChange={async (e) => {
                  const f = e.target.files?.[0];
                  if (!f) return;
                  setUploading(true);
                  try {
                    const fd = new FormData();
                    fd.append('avatar', f);
                    const res = await api.post('/upload/avatar', fd, { headers: { 'Content-Type': 'multipart/form-data' } });
                    const url = res?.data?.fullUrl || res?.data?.url;
                    if (url) setForm(prev => ({ ...prev, avatarUrl: url }));
                  } catch (err) {
                    alert(err?.response?.data?.message || 'Ti nh tht bi (ch h tr jpeg/png/webp/gif, ti a 2MB)');
                  } finally {
                    setUploading(false);
                    try { e.target.value = ''; } catch {}
                  }
                }} />
                <input className="input" name="avatarUrl" value={form.avatarUrl} onChange={change} placeholder="https://...jpg" style={{ minWidth: 260 }} />
              </div>
            </div>
            <div className="empty" style={{ marginTop: 6, fontSize: 12, opacity: 0.8 }}>Bn c th dn URL nh hoc ti nh t my. nh ti a 2MB.</div>
          </div>
          <div className="field">
            <label className="label">S in thoi</label>
            <input className="input" name="phone" value={form.phone} onChange={change} placeholder="V d: 0901234567" />
          </div>
          <div className="field">
            <label className="label">a ch</label>
            <input className="input" name="address" value={form.address} onChange={change} placeholder="S nh, ng, qun/huyn, tnh" />
          </div>
        </>
      )}
      {allowSelfChangePassword && !isAdminEditing && !isModeratorSelf && (
        <div className="row" style={{ marginTop: 12 }}>
          <button className="button button--ghost" type="button" onClick={() => setShowChangePwd(s => !s)}>
            {showChangePwd ? 'n i mt khu' : 'i mt khu'}
          </button>
        </div>
      )}
      {allowSelfChangePassword && !isAdminEditing && showChangePwd && !isModeratorSelf && (
        <div className="card" style={{ marginTop: 8 }}>
          <div className="card__header">i mt khu</div>
          <div className="card__body">
            <div className="row" style={{ gap: 8 }}>
              <button type="button" className="button" onClick={sendOtp} disabled={resending}>{resending ? 'ang gi OTP...' : 'Gi OTP n email ca ti'}</button>
              {remaining > 0 && (
                <span className="badge">OTP cn: {Math.floor(remaining/60)}:{String(remaining%60).padStart(2,'0')}</span>
              )}
            </div>
            {devOtp && (
              <div className="empty" style={{ marginTop: 8, color: '#0a7' }}>
                OTP (dev): <b>{devOtp}</b>
              </div>
            )}
            <div className="form" style={{ marginTop: 12 }}>
              <div className="field">
                <label className="label">OTP</label>
                <input className="input" value={pw.otp} onChange={(e) => setPw({ ...pw, otp: e.target.value })} placeholder="6 ch s" inputMode="numeric" pattern="^\\d{6}$" title="Nhp 6 ch s" required />
              </div>
              <div className="field">
                <label className="label">Mt khu mi</label>
                <input className="input" type="password" value={pw.newPassword} onChange={(e) => setPw({ ...pw, newPassword: e.target.value })} minLength={6} required />
              </div>
              <div className="field">
                <label className="label">Nhp li mt khu mi</label>
                <input className="input" type="password" value={pw.confirm} onChange={(e) => setPw({ ...pw, confirm: e.target.value })} minLength={6} required />
                {pw.newPassword && pw.confirm && pw.newPassword !== pw.confirm && (
                  <div className="empty" style={{ color: '#c62828', marginTop: 6 }}>Mt khu nhp li khng khp</div>
                )}
              </div>
              <div className="row">
                <button className="button" type="button" onClick={changePassword} disabled={cpLoading}>{cpLoading ? 'ang i...' : 'i mt khu'}</button>
              </div>
              {otpMsg && <div className="empty" style={{ marginTop: 8 }}>{otpMsg}</div>}
            </div>
          </div>
        </div>
      )}
      {!isModeratorSelf && (
        <div className="row">
          <button className="button" type="submit" disabled={saving}>{saving ? "ang lu..." : "Lu"}</button>
        </div>
      )}
    </form>
  );
}
